import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from "react-redux";
import { useParams } from "react-router-dom";
import { editContact } from '../Redux/action';


function EditContact() {

    interface Contact {
        id: number;
        first_name: string;
        last_name: string;
        mob: string;
        status: string;
      }
      
      interface State {
        contacts: Contact[];
      }

      
    interface Form {
        first_name: string;
        last_name: string;
        mob: string;
        status: string;
    }


    const { id } = useParams()
    console.log(id)

    const dispatch = useDispatch()

    const AllContact = useSelector((store : State) => store.contacts)



    const [form, setForm] = useState<Form>({
        first_name: '',
        last_name: '',
        mob: '',
        status: ''
    });

    const handleChange = (e : React.ChangeEvent<HTMLInputElement>) => {
     
        setForm({
            ...form,
            [e.target.name]: e.target.value
        })


    }

    const selectHandleChange = (e : React.ChangeEvent<HTMLSelectElement>) => {
        setForm({
            ...form,
            [e.target.name]: e.target.value
        })
    }




    function handleSave() {



        dispatch(editContact({ id, ...form }))

    }

    useEffect(() => {

        AllContact.filter((el) => {
            if(""+el.id === id) {
                setForm(el);
            }
            return false;
        } 
        
        );

    }, [])

    return (
        <div className="w-1/2 mx-auto my-4 pt-16 bg-white rounded-lg shadow-lg p-6">
            <h2 className="text-2xl text-teal-700 font-serif font-bold mb-4">Edit Contact</h2>
            <div className="mb-4">
                <label className="block text-teal-700 font-serif font-bold mb-2 text-left" htmlFor="first-name">
                    First Name
                </label>
                <input
                    className="w-full border border-gray-400 p-2 rounded-md"
                    id="first-name"
                    type="text"
                    name="first_name"
                    value={form.first_name}
                    onChange={handleChange}
                />
            </div>
            <div className="mb-4">
                <label className="block text-teal-700 font-serif font-bold mb-2 text-left" htmlFor="last-name">
                    Last Name
                </label>
                <input
                    className="w-full border border-gray-400 p-2 rounded-md"
                    id="last-name"
                    type="text"
                    name="last_name"
                    value={form.last_name}
                    onChange={handleChange}
                />
            </div>
            <div className="mb-4">
                <label className="block text-teal-700 font-serif font-bold mb-2 text-left" htmlFor="last-name">
                    Mobile Number
                </label>
                <input
                    className="w-full border border-gray-400 p-2 rounded-md"
                    id="last-name"
                    type="number"
                    name="mob"
                    value={form.mob}
                    onChange={handleChange}
                />
            </div>
            <div className="mb-4">
                <label className="block text-teal-700 font-serif font-bold mb-2 text-left" htmlFor="status">
                    Status
                </label>
                <select
                    className="w-full border border-gray-400 p-2 rounded-md"
                    id="status"
                    name="status"
                    value={form.status}
                    onChange={selectHandleChange}
                >
                    <option value={'active'}>Active</option>
                    <option value={"inactive"}>Inactive</option>
                </select>
            </div>
            <br></br>
            <br></br>
            <button
                className="bg-teal-500 hover:bg-teal-700  font-serif text-white font-bold py-2 px-4 rounded text-left"
                onClick={handleSave}
            >
                Save Contact
            </button>
        </div>
    );
}


export default EditContact