import { ADD_CONTACT, EDIT_CONTACT, REMOVE_CONTACT } from "./actionTypes";


interface Contact {
  id: number;
  first_name: string;
  last_name: string;
  mob: string;
  status: string;
}

interface State {
  contacts: Contact[];
}

const storedContacts = localStorage.getItem("contacts");
const initialContacts: Contact[] = storedContacts ? JSON.parse(storedContacts) : [];

const initialState: State = {
  contacts: initialContacts,
};

export default function reducer(state = initialState, action: any): State {
  switch (action.type) {
    case ADD_CONTACT: {
      let flag = 0;
      if (
        action.payload.first_name === "" ||
        action.payload.last_name === "" ||
        action.payload.mob === ""
      ) {
        alert("ohh You Missed Required Input , Please fill");
        flag = 1;
      } else {
        state.contacts.forEach((el) => {
          if (
            el.first_name === action.payload.first_name &&
            el.last_name === action.payload.last_name
          ) {
            alert("Name Already Exist In Contact");
            flag = 1;
          }
        });
      }

      if (!flag) {
        alert("Contact Saved Successfully!!!");

        const updatedContacts = [
          ...state.contacts,
          {
            id: state.contacts.length + 1,
            ...action.payload,
          },
        ];
        localStorage.setItem("contacts", JSON.stringify(updatedContacts));
        return {
          ...state,
          contacts: updatedContacts,
        };
      }
    }
    // Handle other cases and return state for them as well
    case REMOVE_CONTACT: {
      const updatedContacts = state.contacts.filter(contact => contact.id !== action.payload.id);
      localStorage.setItem("contacts", JSON.stringify(updatedContacts));
      return {
        ...state,
        contacts: updatedContacts,
      };
  }
  case EDIT_CONTACT: {
    const updatedContacts = state.contacts.map(contact => {
      if (contact.id === action.payload.id) {
        return {
          ...contact,
          ...action.payload,
        };
      }
      return contact;
    });
    localStorage.setItem("contacts", JSON.stringify(updatedContacts));
    return {
      ...state,
      contacts: updatedContacts,
      };
    }

    default:
      return state;

    
  }
}

export const state = {};